import Mimc from '@/assets/js/mimc/mimc.js'
import HTTP from '@/http.js'

const MIMC = new Mimc()

const mimc = {
  state: {
    isNewMessage: false, // 
    userMessage: [], // json
    currentUser: '', // 
    historyMessages: [], // 
  },

  mutations: {
    globalEvent (state, message) {
      if (message.isNewMessage !== undefined) state.isNewMessage = message.isNewMessage
      if (message.userMessage !== undefined) state.userMessage = message.userMessage
      if (message.currentUser !== undefined) state.currentUser = message.currentUser
      if (message.historyMessages !== undefined) state.historyMessages = message.historyMessages
    },

    // 
    mimcLogin (state, message) {
      MIMC.login(message)
    },

    // 
    addMessage (state, message) {
      state.historyMessages.push(message)
    },
  },

  actions: {
    globalEvent ({ commit }, message) {
      commit('globalEvent', message)
    },

    // mimctoken
    async fetchGetMIMCToken ({ commit }, message) {
      await MIMC.fetchGetMIMCToken(message)
    },

    // 
    async getConversationList ({ commit }, message) {
      let res = await MIMC.getConversationList() // 
      let list = await Promise.all(res.data.contacts.map(async (item) => {
        let unreadMsg = ''
        if (item.lastMessage.msgExtra === '0' && item.lastMessage.fromAccount === item.name) {
          commit('globalEvent', { isNewMessage: true })
          let messageHistory = await this.dispatch('getTotalUnreadCount', item.name) // 
          console.log('messageHistory', messageHistory)
          unreadMsg = messageHistory.data.messages.filter((messagesFilterItem) => messagesFilterItem.extra === '0' && messagesFilterItem.fromAccount !== localStorage.userId) // 
        }
        let userData = await this.dispatch('apiFindByIdUserData', item.name) // api
        return {
          unreadMsg: unreadMsg,
          fromAccount: userData.data,
          isRead: item.lastMessage.msgExtra
        }
      }))
      let alreadyRead = list.filter((item) => item.isRead === '0') // 
      if (alreadyRead.length === 0) {
        commit('globalEvent', { isNewMessage: false }) // 
      }
      commit('globalEvent', { userMessage: list })
    },

    // api
    async apiFindByIdUserData ({ commit }, message) {
      return await HTTP.http.post(`/woho/sys/user/findById/${message}`)
    },

    // 
    async getTotalUnreadCount ({ commit }, message) {
      return await MIMC.messageHistory(message, 100, 0)
    },

    // 
    clearUnreadCount ({ commit }, message) {
      try {
        message.sequence.forEach(element => {
          let data = {
            toAccount: localStorage.userId,
            mimc_appAccount: message.mimc_appAccount,
            sequence: element.sequence,
            extraValue: 1,
          }
          MIMC.updateExtra(data)
        })
      } catch (error) { error }
      this.dispatch('getConversationList')
    },

    // 
    async getHistoryMessages ({ commit }, message) {
      commit('globalEvent', { currentUser: message })
      let historyMessages = await MIMC.messageHistory(message.id, 100)
      // console.log('historyMessages', historyMessages.bizType)
      this.dispatch('base64Messages', historyMessages.data.messages)
    },

    // base64
    base64Messages ({ commit }, message) {
      let historyMessages = message.map((item) => {
        item.message = {
          /* eslint-disable */
          ...JSON.parse(Base64.decode(item.payload))
        }
        item.message.payload = Base64.decode(JSON.parse(Base64.decode(item.payload)).payload)
        if (item.bizType) {
          item.message.bizType = item.bizType
        }
        return item
      })
      commit('globalEvent', { historyMessages: historyMessages })
    },

    // 
    sendMsg ({ commit }, message) {
      let data = {
          userName: message.userName,
          sendMessage: message.sendMessage,
          bizType: message.bizType
        }
      MIMC.sendMsg(data)
    },

    // 
    clearCurrentUser ({ commit }, message) {
      commit('globalEvent', { currentUser: '' })
    }
  }
}

export default mimc
