<template>
  <div class="box-card">
    <LayoutBox one="">
      <h4></h4>
      <el-form ref="shopDataForm" class="form_group" :model="shopData" :rules="rules" label-width="130px">
        <el-form-item label="" prop="mobile">
          <el-input v-model.number="shopData.mobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="area">
          <areaSelectCmp :area="shopData.area" @childFn="onChild"></areaSelectCmp>
        </el-form-item>
        <el-form-item prop="adderss">
          <el-input v-model="shopData.adderss" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="describes">
          <el-input v-model="shopData.describes" type="textarea" :autosize="{ minRows: 6, maxRows: 6}" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="deliveryRule">
          <el-input v-model="shopData.deliveryRule" type="textarea" :autosize="{ minRows: 6, maxRows: 6}" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <p class="remark">
            
            
            
            
            
          </p>
        </el-form-item>
        <el-form-item>
          <el-button class="inquire" type="success" style="width: 120px" round @click="submitForm('shopDataForm')"></el-button>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import areaSelectCmp from '@/components/area_select_cmp/index'
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'ShopInfoEdit',

  components: {
    areaSelectCmp,
    LayoutBox
  },

  data () {
    return {
      shopData: {
        mobile: '',
        area: '',
        adderss: '',
        describes: '', // 
        deliveryRule: ''
      },
      rules: {
        mobile: [
          { required: true, message: '', trigger: 'blur' }
        ],
        area: [
          { required: true, message: '', trigger: 'blur' }
        ],
        adderss: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },

  created () {
    this._getShopData()
      .then((res) => {
        this.shopData = { ...res }
        this.shopData.area = res.area.split(',')
      })
  },

  mounted () {
  },

  methods: {
    // 
    onChild (e) {
      let area = e[0] + ',' + e[1]
      if (e.length === 3) {
        area = area + ',' + e[2]
      }
      this.shopData.area = area
    },
    // 
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this._updateShop()
            .then((res) => {
              if (res) {
                this.$message.success('')
              }
            })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // api
    // 
    _getShopData () {
      return this.$http.post('/woho/shop/manage/findByUserId')
        .then((res) => {
          return res.data.data
        })
    },
    // 
    _updateShop () {
      // console.log(this.shopData.area.constructor == Array)
      // eslint-disable-next-line
      if (this.shopData.area.constructor == Array) {
        this.shopData.area = this.shopData.area.join(',')
      }
      return this.$http.post('/woho/shop/manage/update', this.shopData)
        .then((res) => {
          if (!res.data.success) this.$message.error(res.data.msg)
          return res.data.success
        })
    }
  }
}
</script>

<style lang="scss" scoped>
  .box-card {
    min-width: 1100px;
    .form_group{
      width: 430px;
      .remark{
        line-height: 20px;
      }
    }
  }
</style>
